main {
  margin-bottom: 3rem;
}
@media (max-width: 768px) {
  main {
    display: flex;
    flex-direction: column-reverse;
    .sec-2 {
      width: 100%;
      img {
        width: 100%;
      }
    }
    .sec-1 {
      text-align: center;
      padding: 1rem 0;
      h1 {
        padding: 1rem;
        font-size: 32px;
      }
      p {
        padding: 1rem;
        line-height: 1.3rem;
        color: $color-2;
      }
      button {
        padding: 0.9rem 1.3rem;
        border-radius: 0.8rem;
        border: none;
        color: $color-1;
        background-color: $color-3;
        margin-bottom: 1rem;
      }
      .container {
        display: flex;
        justify-content: space-between;
        padding: 0 0.2rem;
        margin-top: 1rem;
        img {
          width: 80px;
          height: 27px;
        }
      }
    }
  }
}
@media (min-width: 769px) {
  main {
    display: flex;
    width: 78%;
    margin: auto;
    margin-bottom: 3rem;
    justify-content: space-between;
    .sec-1 {
      width: 50%;
      padding-top: 8rem;
      h1 {
        font-size: 50px;
        margin-bottom: 2rem;
        span {
          display: block;
        }
      }
      p {
        width: 70%;
        color: $color-2;
        margin-bottom: 2rem;
        line-height: 1.5rem;
      }
      button {
        padding: 0.9rem 1.3rem;
        border-radius: 0.8rem;
        border: none;
        color: $color-1;
        background-color: $color-3;
        margin-bottom: 5rem;
        transition: 0.7s all;
        cursor: pointer;
        &:hover {
          background-color: white;
          border: 1px solid $color-3;
          color: $color-3;
        }
      }
      .container {
        display: flex;
        width: 60%;
        gap: 2rem;
      }
    }
    .sec-2 {
      width: 50%;
      height: 80vh;
      text-align: end;
      img {
        width: auto;
        height: 70vh;
      }
    }
  }
}
